* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-title {
  display: flex;
  align-items: center;
}

.country-details {
  font-size: 22px;
}

.country-flag {
  width: 32px;
  height: 20px;
}

.country-list {
  list-style-type: none;
  margin: 0;
}

.country-list-item {
  display: flex;
  align-items: center;
  font-size: 22px;
  margin-bottom: 5px;
}